.layer {
    display: block;
    position: fixed;
    _position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
}

.layer .dimBg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    opacity: .5;
    filter: alpha(opacity=50);
}

.layer .pop-layer {
    display:block;
}

.pop-layer {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 410px;
    height: auto;
    background-color: #fff;
    border: 5px solid #3571B5;
    z-index: 10;
  }

  .pop-layer .pop-container {
    padding: 20px 25px;
  }

  .show-more-img {
    width:50px; 
    height:80px; 
